<script>
import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
import 'vue-awesome/icons/flag'
import Icon from 'vue-awesome/components/Icon'

Vue.use(VueAxios, axios)
Vue.component('icon', Icon)
Vue.component('card', {
  template: `
    <div class="card-wrap"
      
      ref="card">
      <div class="card"
        :style="cardStyle">
        <div class="card-bg" :style="[cardBgTransform]">
         <img :src="this.dataImage" alt='Photo-stock'>
         {{this.width}}
        </div>
        <div class="card-info">
          <slot name="header"></slot>
          <slot name="content"></slot>
        </div>
      </div>
    </div>`,
  mounted() {
    this.width = this.$refs.card.offsetWidth;
    console.log(this.width)
    this.height = this.$refs.card.offsetHeight;
  },
  props: ['dataImage'],
  data: () => ({
    width: 0,
    height: 0,
    mouseX: 0,
    mouseY: 0,
    mouseLeaveDelay: null
  }),
  computed: {
    mousePX() {
      return this.mouseX / this.width;
    },
    mousePY() {
      return this.mouseY / this.height;
    },
    cardStyle() {
      const rX = this.mousePX * 30;
      const rY = this.mousePY * -30;
      return {
        transform: `rotateY(${rX}deg) rotateX(${rY}deg)`
      };
    },
    cardBgTransform() {
      const tX = this.mousePX * -40;
      const tY = this.mousePY * -40;
      return {
        transform: `translateX(${tX}px) translateY(${tY}px)`
      }
    },
    cardBgImage() {
      return {
        backgroundImage: `url(${this.dataImage})`
      }
    }
  },
  methods: {
    handleMouseMove(e) {
      this.mouseX = e.pageX - this.$refs.card.offsetLeft - this.width/2;
      this.mouseY = e.pageY - this.$refs.card.offsetTop - this.height/2;
    },
    handleMouseEnter() {
      clearTimeout(this.mouseLeaveDelay);
    },
    handleMouseLeave() {
      this.mouseLeaveDelay = setTimeout(()=>{
        this.mouseX = 0;
        this.mouseY = 0;
      }, 1000);
    }
  }
});

export default {
  name: 'app',
  data () {
    return {
      msg: 'Welcome to Photo-stock App',
      search:'',
      status:'',
      items:[
        {full_path:baseUrl+"assets/themes/default/images/placeholder.jpg"},
        {full_path:baseUrl+"assets/themes/default/images/placeholder.jpg"},
        {full_path:baseUrl+"assets/themes/default/images/placeholder.jpg"},
        {full_path:baseUrl+"assets/themes/default/images/placeholder.jpg"},
        {full_path:baseUrl+"assets/themes/default/images/placeholder.jpg"},
        {full_path:baseUrl+"assets/themes/default/images/placeholder.jpg"},
        {full_path:baseUrl+"assets/themes/default/images/placeholder.jpg"},
        {full_path:baseUrl+"assets/themes/default/images/placeholder.jpg"},
        {full_path:baseUrl+"assets/themes/default/images/placeholder.jpg"},
        {full_path:baseUrl+"assets/themes/default/images/placeholder.jpg"},
        {full_path:baseUrl+"assets/themes/default/images/placeholder.jpg"},
        {full_path:baseUrl+"assets/themes/default/images/placeholder.jpg"},
        {full_path:baseUrl+"assets/themes/default/images/placeholder.jpg"},
        {full_path:baseUrl+"assets/themes/default/images/placeholder.jpg"}
         ]
    }
  },
  methods:{
    fetchPosts(){
      var self = this;
      this.axios.get(baseUrl+'post/get_list/20')
        .then(function(data) {            
            data = data.data;
            console.log(data.results);
            self.items = data.results;
            console.log('submited');                       
        })
        .catch(function(err) {
            console.log('error ocursdf');
            console.log(err);
        });
    },
    inputChangeEvent:function(){
        /* make api request on events filter */
        var self = this;
        
        this.$http.get($('.pageUrls').data('listurl')+'?page_size='+self.page_size+'&q='+this.search+'&status='+this.status)
            .then(function(data){
                data = JSON.parse(data.body);
                this.items = data.results;
                this.totalPages = precisionRound(parseFloat(data.total_pages),0);                
                
            }, function(error){
                console.log(error.statusText);
        });
    }
  },
  mounted: function(){
    this.fetchPosts();
  }
}
</script>

<style lang="scss">
$hoverEasing: cubic-bezier(0.23, 1, 0.32, 1);
$returnEasing: cubic-bezier(0.445, 0.05, 0.55, 0.95);



.title {
  font-family: "Raleway";
  font-size: 24px;
  font-weight: 700;
  color: #5D4037;
  text-align: center;
}

p {
  line-height: 1.5em;
}

h1+p, p+p {
  margin-top: 10px;
}

.card-wrap {
  margin: 10px;
  transform: perspective(800px);
  transform-style: preserve-3d;
  cursor: pointer;
  // background-color: #fff;
  
  &:hover {
    .card-info {
      transform: translateY(0);
    }
    .card-info p {
      opacity: 1;
    }
    .card-info, .card-info p {
      transition: 0.6s $hoverEasing;
    }
    .card-info:after {
      transition: 5s $hoverEasing;
      opacity: 1;
      //transform: translateY(0);
    }
    .card-bg {
      transition: 
        //0.6s $hoverEasing,
        opacity 5s $hoverEasing;
      opacity: 0.8;
    }
    .img {
      -ms-transform: scale(1.1);
      transform:
        scale(1.1);
      transition:
        0.6s $hoverEasing,
        box-shadow 2s $hoverEasing;
      box-shadow:
        rgba(white, 0.2) 0 0 40px 5px,
        rgba(white, 1) 0 0 0 1px,
        rgba(black, 0.66) 0 30px 60px 0,
        inset #333 0 0 0 5px,
        inset white 0 0 0 6px;
    }
  }
}

.card {
  position: relative;
  flex: 0 0 332px;
  // width: 332px;
  height: 221px;
  // background-color: #333;
  overflow: hidden;
  
}
.card img{
  // box-shadow: 0 30px 10px 0 rgba(0,0,0,.66),inset 0 0 0 5px #333,inset 0 0 0 6px hsla(0,0%,100%,.5);
  transition: 1s cubic-bezier(.445,.05,.55,.95);
}
.card-bg {
  // opacity: 0.5;
  position: absolute;
  top: -20px; left: -20px;
  width: 100%;
  height: 100%;
  padding: 20px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  transition:
    1s $returnEasing,
    opacity 5s 1s $returnEasing;
  pointer-events: none;
}

.card-info {
  padding: 20px;
  position: absolute;
  bottom: 0;
  color: #fff;
  transform: translateY(40%);
  transition: 0.6s 1.6s cubic-bezier(0.215, 0.61, 0.355, 1);
  
  p {
    opacity: 0;
    text-shadow: rgba(black, 1) 0 2px 3px;
    transition: 0.6s 1.6s cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  
  * {
    position: relative;
    z-index: 1;
  }
  
  &:after {
    content: '';
    position: absolute;
    top: 0; left: 0;
    z-index: 0;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(to bottom, transparent 0%, rgba(#000, 0.6) 100%);
    background-blend-mode: overlay;
    opacity: 0;
    transform: translateY(100%);
    transition: 5s 1s $returnEasing;
  }
}

.card-info h1 {
  font-family: "Playfair Display";
  font-size: 36px;
  font-weight: 700;
  text-shadow: rgba(black, 0.5) 0 10px 10px;
}

</style>



